.c-comment {
  display: flex;
  flex-direction: column;
  @include bp($xs) {
    flex-direction: row-reverse;
  }

  &__picture {
    min-height: 264px;
    width: 100%;
    background: url("../images/yellow-bg.png") no-repeat center right;
    background-size: cover;
    position: relative;
    @include bp($xs) {
      min-width: 120px;
    }

    &:before {
      display: block;
      content: '';
      position: absolute;
      top: 0;
      right: 50%;
      @include triangle(10px, white, down);
      @include bp($xs) {
        right: 0;
        top: 50%;
        border-right-color: white;
        border-top-color: transparent;
      }
    }
  }

  &__body {
    background: $white;
    padding: 30px 20px 30px 35px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .c-social-stat {
      flex-direction: row;
    }
  }
}

